import { lazy } from "react";
const TeachersCard = lazy(() => import("./TeachersCard"));

const Teachers = ({ activeTeachers }) => {
  return (
    <section
      className={`teachers container ${
        activeTeachers ? "teachers-click-style" : ""
      }`}
    >
      <h2 data-aos="fade-right" data-aos-duration="600" data-aos-once="true">
        Ustozlar
      </h2>
      <div className="row gx-5">
        <TeachersCard />
      </div>
    </section>
  );
};

export default Teachers;
